import Header from './Header'
import GalleriaHead from './GalleriaHead'


const layoutStyle = {
  margin: 20,
  padding: 20,
  border: '1px solid #DDD'
}

const Layout = (props) => (
  <div style={layoutStyle}>
    <GalleriaHead />
    <Header />
    {props.children}
  </div>
)

export default Layout



